.button {
  position: relative;
  display: block;
  width: 100%;
  padding: 8px;
  font-size: 20px;
  cursor: pointer; }
  .button--busy {
    pointer-events: none;
    opacity: 0.7; }
    .button--busy::after {
      content: "";
      position: absolute;
      right: 10px;
      top: 7px;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      border: 3px solid #aaa;
      border-right: 3px solid #555;
      -webkit-animation: spin 0.8s infinite;
              animation: spin 0.8s infinite; }

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



